<template>
    <section class="notes">
        <div class="container">
            <div class="notes__content">
                <h2 class="notes__content-title">{{ notes.length ? 'All' : 'No' }}  notes</h2>
                <button class="notes__content-checker" @click="view = !view">
                    <img src="@/assets/img/icons/list.svg" alt="" class="notes__content-icon" v-show="view">
                    <img src="@/assets/img/icons/grid.svg" alt="" class="notes__content-icon" v-show="!view">
                    <b>{{ view ? 'List' : 'Grid' }}</b>
                </button>
            </div>
            <div class="notes__cards" :class="{ grid: !view }">
                <TransitionGroup name="notes__cards">
                    <NotesItem v-for="item in notes" :key="item" :note="item" @delTask="$emit('delTask', item.id)" @editTask ="$emit('editTask', item.id)"/>
                </TransitionGroup>
            </div>
        </div>
    </section>
</template>

<script>

import { TransitionGroup } from "vue";
import NotesItem from "./NotesItem.vue";

export default {
    name: "Notes",
    data() {
        return {
            view: true
        }
    },
    props: {
        notes: {
            type: Array,
            default: []
        }
    },
    components: {
        NotesItem,
        TransitionGroup,
        TransitionGroup
    }
}

</script>